import React, { Component } from 'react';
import './App.css';
import './app.less';
import { BrowserRouter, Route, Link} from 'react-router-dom'
import { createStore} from 'redux'
import { Provider, connect } from 'react-redux'

class CounterIncrease extends Component {
    render() {
        const { value, onIncreaseClick} = this.props
        return (
            <div>
                <span className="test">{value}</span>
                <button onClick={onIncreaseClick}>Increase</button>
            </div>
        )
    }
}

class CounterMin extends Component {
    render() {
        const { onMinClick } = this.props
        return (
            <div>
                <button onClick={onMinClick}>Min</button>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        value: state.count
    }
}

function mapDispatchToProps(dispatch) {
    return {
        onIncreaseClick: () => dispatch(increaseAction),
        onMinClick: () => dispatch(minAction)
    }
}

const increaseAction = { type: 'increase' }
const minAction = { type: 'min' }

const Bpp = connect(
    mapStateToProps,
    mapDispatchToProps
)(CounterIncrease)

const Cpp = connect(
    mapStateToProps,
    mapDispatchToProps
)(CounterMin)

function counter(state = { count: 0 }, action) {
    const count = state.count
    switch (action.type) {
        case 'increase':
            return { count: count + 1 }
        case 'min':
            return { count: count - 1 }
        default:
            return state

    }
}

const store = createStore(
    counter
)

class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            value: ''
        }

    }
    componentDidMount() {
    }
    handleChange (e) {
        console.log(e.target.value)
        this.setState({
            value: e.target.value ? e.target.value : ''
        })
    }

     render() {
        return (
          <div className="App">
            <input type="text" value={this.state.value} onChange={(e) => this.handleChange(e)} />
            {this.state.value}
            <RouteList />
            <Provider store={store}>
                <div>
                    <Bpp />
                    <Cpp />
                </div>

            </Provider>
          </div>
        );
     }
}

const One = () => (
    <div>
        <h2>首页</h2>
    </div>
)

const Two = () => (
    <div>
        <h2>第二页</h2>
    </div>
)

const Three = ({ match }) => (
    <div>
        <h2>第三页{match.url}</h2>
    </div>
)

const RouteList = () => (
    <BrowserRouter>
        <div>
            <Link to = '/'>首页</Link>
            <Link to = '/two'>二</Link>
            <Link to = '/three'>三</Link>
            <Route exact path='/' component = {One} />
            <Route exact path='/two' component = {Two} />
            <Route exact path='/three' component = {Three} />
        </div>
    </BrowserRouter>
)
export default App;



